import React from 'react';
import styled from 'styled-components';

const Div = styled.div `
	display:flex;
	align-items:center;
	justify-content:space-between;
	width:${(window.innerWidth > 600) ? '130px' : '20vw'};
`;
const SDiv = styled.div`
	display:flex;
	width:${(window.innerWidth > 600) ? '70px' : '15vw'};
	justify-content:space-between;
`;
const Direction = styled.span`
	padding:5px 5px;
	background:#ccc;
	cursor:pointer;

`;
const Box = styled.span`
	padding:5px 7px;
	background:#ddd;
	cursor:pointer;
`;


function Pagination() {

	return(
		<Div>
			<Direction>{'<'}</Direction>
			<SDiv>
				<Box style={{background:'lightblue'}}>1</Box>
				<Box>2</Box>
				<Box>3</Box>
			</SDiv>
			<Direction>{'>'}</Direction>
		</Div>
	);
}

export default Pagination; 